@font-face {
    font-family: 'Montserrat Light';
    src: url('../fonts/Montserrat-Light.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@import '_variables.scss';
@import '_mixins.scss';

// Note.scss
.star-container {
    display: flex;
    align-items: center;
    justify-content: flex-end; /* Alignez les étoiles à droite par défaut */
    width: auto; /* Ajustez la largeur automatiquement en fonction du contenu */
    height: auto; /* Ajustez la hauteur automatiquement en fonction du contenu */
    padding-right: 0em;
}

.star_full {
    width: 24.75px; /* Ajustez la taille des étoiles */
    height: 24px; /* Ajustez la taille des étoiles */
    margin-right: 8px; /* Ajoutez une marge à droite pour séparer les étoiles */
}

.star_empty {
    width: 24.75px; /* Ajustez la taille des étoiles */
    height: 24px; /* Ajustez la taille des étoiles */
    margin-right: 8px; /* Ajoutez une marge à droite pour séparer les étoiles */
}

@include respond-to('mobile') {
    .star-container {
        justify-content: flex-start; /* Alignez les étoiles à gauche pour mobile */
        padding-left: 0em; /* Ajoutez du padding à gauche pour un espace ?>2vw;<? */
        padding-right: 0; /* Annule la marge à droite */
        width: 100%; /* Prendre toute la largeur disponible */
    }
}
