@import '_variables.scss';
@import '_mixins.scss';
@font-face {
    font-family: 'Montserrat Light';
    src: url('../fonts/Montserrat-Light.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

* {
    font-family: 'Montserrat Light', sans-serif;
}

.equipement-list-container {
    position: relative;
    z-index: 2;
    width: 100%;
    border-radius: 10px;
    margin-bottom: 20px;
    padding: 3em;
    /* padding-right: 14em; */
}

.equipement-header {
    padding: 2vw;
    cursor: pointer;
    display: flex;
    height: 47px;
    align-items: center;
    justify-content: space-between;
    background: #FF6060;
    border-radius: 10px;
    color: #f5f5f5;
}

.menu-title {
    font-size: 1.75em;
    background-color: #FF6060;
    padding: 0em 0em;
    border-radius: 5px;
    cursor: pointer;
}

.menu-icon {
    width: 24px;
    height: 14px;
    transition: transform 0.3s ease;
}

.menu-icon.open {
    transform: rotate(-180deg);
}

.equipement-content {
    background-color: #F6F6F6; /* Ajusté pour correspondre à Collapse */
    border-bottom-right-radius: 10px; /* Ajusté pour correspondre à Collapse */
    border-bottom-left-radius: 10px; /* Ajusté pour correspondre à Collapse */
    font-size: 1em; /* Ajusté pour correspondre à Collapse */
    line-height: 1.5em; /* Ajusté pour correspondre à Collapse */
    overflow: hidden; /* Ajusté pour correspondre à Collapse */
    transition: height 0.3s ease;
    padding: 1em;
}

.equipement-item {
    color: #FF6060;
    margin-bottom: 0.5em;
}

.equipement-list-container.open .equipement-content {
    height: auto;
}

.equipement-name {
    font-size: 1.5em;
    color: #FF6060;
}

.equipement-item.open {
    height: auto;
}
@include respond-to('tablet') {
    /* Styles pour tablette */
    .menu-title {
      font-size: 1.25em;
      background-color: #FF6060;
      padding: 0em 0em;
      border-radius: 5px;
      cursor: pointer;
  }
}

@include respond-to('desktop') {
    /* Styles pour desktop */
  
    .menu-title {
      font-size: 1.25em;
      background-color: #FF6060;
      padding: 0em 0em;
      border-radius: 5px;
      cursor: pointer;
  }
}
@include respond-to('mobile') {
    .equipement-header {
        padding: 1.25em; /* Ajusté pour correspondre au padding de Collapse */
        cursor: pointer;
        display: flex;
        height: 30px; /* Ajusté pour correspondre à Collapse */
        align-items: center;
        justify-content: space-between;
        background: #FF6060;
        border-radius: 10px;
        color: white;
    }
    .round-image-container .profile-image {
        width: 64px;
        height: 64px;
        border-radius: 50%;
        object-fit: cover;
    }

    .menu-title {
        font-size: 1.1em;
        background-color: #FF6060;
        padding: 0em 0em;
        border-radius: 5px;
        cursor: pointer;
    }
    .note-container {
        margin-left: initial;
        height: 0em;
        margin-left: 0em;
      }
    .equipement-list-container {
        position: relative;
        z-index: 2;
        width: 90%;
        border-radius: 10px;
        margin-bottom: 20px;
        padding: 0em;
        left: 50%;
        transform: translateX(-50%);
        /* padding-right: 14em; */
    }
}
